@import url(https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,200;0,400;1,400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* general styles */
:root {
    --blue: #3685cf;
    --pale-mint: #cbdaf5;
    --cream: #f1f0f5;
    --deep-marroon: #785964;
    --onyx: #0a0a0a;
}

* {
    margin: 0;
    padding: 0 0;
}

html, body, #root, main, ul {
    height: 100%;
    margin: 0;
}

#root {
    background-color: #cbdaf5;
    background-color: var(--pale-mint);
}


p, label {
    font-family: 'Raleway', sans-serif;
}

a {
    text-decoration: none;
}

li {
    color: #3685cf;
    color: var(--blue);
    list-style-type: none;
}

h2 {
    color: #0a0a0a;
    color: var(--onyx);
    font-family: 'Raleway', sans-serif;
}

.title {
    color: #0a0a0a;
    color: var(--onyx);
}


/* header */
header {
    background-color: #0a0a0a;
    background-color: var(--onyx);
    height: 8%;
}

.initials {
    font-family: 'Raleway', sans-serif;
    font-weight: 400;
    font-style: italic;
    color: #3685cf;
    color: var(--blue);
    -webkit-align-self: center;
            align-self: center;
    padding-left: 8%;
    margin-bottom: 0;
}

/* nav */
.navActive {
    color: #f1f0f5;
    color: var(--cream);
    font-weight: bold;
}

/* about me */
.about-me {
    width: 100%;
    background-color: #3685cf;
    background-color: var(--blue);
    padding: 3%;
}

.about-me img {
    max-width: 100%;
    border-radius: 0%;
    padding: 2% 2%;
}

.about-me p {
    font-size: 20px;
}

/* portfolio */
.portfolio {
    background-color: #3685cf;
    background-color: var(--blue);
    width: 100%;
}

.portfolio h1 {
    font-family: 'Raleway', sans-serif;
    font-weight: 400;
    color: #0a0a0a;
    color: var(--onyx);
}


.project-text a{
    color: #0a0a0a;
    color: var(--onyx);
}

/* contact */
.contact {
    background-color: #3685cf;
    background-color: var(--blue);
    width: 100%;
}


button {
    width: 80% !important;
    color: #f1f0f5 !important;
    color: var(--cream) !important;
    background-color: #0a0a0a !important;
    background-color: var(--onyx) !important;
}

/* resume */
.resume {
    background-color: #3685cf;
    background-color: var(--blue);
    list-style-type: square;
    width: 100%;
    padding: 3%;
}

/* footer */
footer {
    background-color: #0a0a0a;
    background-color: var(--onyx);
    height: 8%;
}

footer a {
    color: #3685cf;
    color: var(--blue);
}

footer a:hover {
    color: #f1f0f5;
    color: var(--cream);
}

